import React from 'react';

class Background extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    const canvasB = this.refs.background;
    const ctx = canvasB.getContext('2d');
    ctx.fillStyle = 'rgb(132, 132, 132)';
    for (let i = 0; i < canvasB.height; i++) {
      let j = (i % 2 === 0) ? 0 : 1;
      while (j < canvasB.width) {
        ctx.fillRect(i, j, 1, 1);
        j += 2;
      }
    }
  }

  render() {
    return (
      <canvas
        ref="background"
        className="background-canvas"
        width={this.props.canvasDim.width}
        height={this.props.canvasDim.height}
      />
    );
  }
}

export default Background;
