import APNGencoder from '../adds/canvas2apng';

const GifEncoder = require('gif-encoder');

function downloadFile(href, name) {
  const link = document.createElement('a');
  link.setAttribute('href', href);
  link.setAttribute('download', name);
  link.click();
}

function Saver(frames, rate) {
  const canvas = document.querySelector('.frames').querySelectorAll('.canvas');
  if (frames.length === 1) {
    // const gif = new GifEncoder(32, 32);
    // gif.setFrameRate(rate);
    // const pixels = canvas[0].getContext('2d').getImageData(0, 0, 32, 32).data;
    // gif.writeHeader();
    // gif.addFrame(pixels);
    // gif.finish();
    // const gif8 = gif.stream();
    // gif.on('readable', function () {
    //   console.log(gif.read());
    // });
    // const hrefGIF = URL.createObjectURL(new Blob([gif8], { type: 'image/gif' }));
    // downloadFile(hrefGIF, 'Piskel-clone.gif');
    const hrefPNG = frames[0];
    downloadFile(hrefPNG, 'Piskel-clone.png');
  } else {
    const encoder = new APNGencoder(canvas);
    encoder.setRepeat(0);
    encoder.setDelay(100 / rate);
    encoder.setDispose(0);
    encoder.setBlend(1);
    encoder.start();
    for (let i = 0; i < frames.length; i += 1) {
      encoder.addFrame(canvas[i]);
    }
    encoder.finish();
    const out = encoder.stream();
    const unit8 = new Uint8Array(out.bin);
    const hrefAPNG = URL.createObjectURL(new Blob([unit8], { type: 'image/png' }));
    downloadFile(hrefAPNG, 'Piskel-clone.apng');
  }
}

export default Saver;
