import React from 'react';
import '../styles/app.scss';
import Popup from './adds/Popup.jsx';
import Header from './header/Header.jsx';
import Tools from './tools/Tools.jsx';
import Frames from './frames/Frames.jsx';
import Tablet from './tablet/Tablet.jsx';
import Animation from './animation/Animation.jsx';
import Footer from './footer/Footer.jsx';
import Saver from './savers/Saver';

class Controller extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      setDim: true,
      resize: false,
      canvasDim: {
        width: undefined,
        height: undefined,
      },
     activeTool: 'pen',
      colors: {
        primary: undefined,
        secondary: undefined,
      },
      idActiveFrame: undefined,
      updateFrame: false,
      framesInData: [],
      rate: undefined,
    };
    this.setCanvasDim = this.setCanvasDim.bind(this);
    this.handleUpdateDim = this.handleUpdateDim.bind(this);
    this.updateCanvas = this.updateCanvas.bind(this);
    this.updateActiveFrame = this.updateActiveFrame.bind(this);
    this.handleActivateTool = this.handleActivateTool.bind(this);
    this.handlePickColor = this.handlePickColor.bind(this);
    this.handleFramesDataChange = this.handleFramesDataChange.bind(this);
    this.handleOnSave = this.handleOnSave.bind(this);
    this.handleUpdateRate = this.handleUpdateRate.bind(this);
  }

  componentDidMount() {
    // localStorage.clear();
    if (localStorage.getItem('AppState')) {
      this.setState((state) => {
        const dat = JSON.parse(localStorage.getItem('AppState'));
        for (const key in state) {
          state[key] = dat[key];
        }
        return {
          state,
        };
      });
    }
  }

  componentDidUpdate() {
    localStorage.setItem('AppState', JSON.stringify(this.state));
  }

  setCanvasDim(dim, pos) {
    this.setState({
      setDim: false,
      resize: true,
      canvasDim: {
        width: dim,
        height: dim,
      },
    });
  }

  handleUpdateDim() {
    this.setState({
      setDim: true,
    });
  }

  handleActivateTool(tool) {
    this.setState({
      activeTool: tool,
    });
  }

  handlePickColor(prim, sec) {
    this.setState({
      colors: {
        primary: prim,
        secondary: sec,
      },
    });
  }

  updateActiveFrame(canvasWillUpdate) {
    this.setState({
      updateFrame: canvasWillUpdate,
    });
  }

  updateCanvas(canvasFromFrame) {
    this.setState({
      idActiveFrame: canvasFromFrame,
      updateFrame: false,
    });
  }

  handleFramesDataChange(frames) {
    this.setState({
      framesInData: frames,
      updateFrame: false,
    });
  }

  handleOnSave() {
    const { framesInData } = this.state;
    const { rate } = this.state;
    if (framesInData.length > 0) {
      Saver(framesInData, rate);
    } else {
      alert('Nothing for save');
    }
  }

  handleReset() {
    localStorage.clear();
  }

  handleUpdateRate(r) {
    this.setState({
      rate: r,
    });
  }

  render() {
    return (
      <div className="wrapper">
        <Header
          onSave={this.handleOnSave}
          onReset={this.handleReset}
        />
        <main>
          <Tools
            activeTool={this.state.activeTool}
            onActivateTool={this.handleActivateTool}
            onPickColor={this.handlePickColor}
            colors={this.state.colors}
          />
          <Frames
            canvasDim={this.state.canvasDim}
            onFramesChange={this.updateCanvas}
            onFramesDataChange={this.handleFramesDataChange}
            updateFrame={this.state.updateFrame}
          />
          <Tablet
            canvasDim={this.state.canvasDim}
            frameId={this.state.idActiveFrame}
            tool={this.state.activeTool}
            colors={this.state.colors}
            onPickColor={this.handlePickColor}
            onCanvasChange={this.updateActiveFrame}
            onChangeCoords={this.updateFooterCoords}
          />
          <Animation
            canvasDim={this.state.canvasDim}
            arrFrames={this.state.framesInData}
            updateRate={this.handleUpdateRate}
          />
        </main>
        <Footer
          canvasDim={this.state.canvasDim}
          onUpdateDim={this.handleUpdateDim}
        />
        <Popup
          setDim={this.state.setDim}
          resize={this.state.resize}
          onCanvasDimChange={this.setCanvasDim}
        />
      </div>
    );
  }
}

export default Controller;
