import React from 'react';
import { act } from 'react-dom/test-utils';
import '../../styles/tools.scss';
import Tool from './tool.jsx';

class Tools extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tools: [
        { id: 1, name: 'pen' },
        { id: 2, name: 'eraser' },
        { id: 3, name: 'line' },
        { id: 4, name: 'bucket' },
        { id: 5, name: 'border' },
        { id: 6, name: 'rectangle' },
        { id: 7, name: 'disc' },
        { id: 8, name: 'circle' },
        { id: 9, name: 'move' },
        { id: 10, name: 'lighten' },
        { id: 11, name: 'picker' },
      ],
      colors: {
        primary: '#000000',
        secondary: '#ffffff',
      },
    };
  }

  componentDidMount() {
    this.stylingActive();
    this.coloredBut();
    this.props.onPickColor(this.state.colors.primary, this.state.colors.secondary);
  }

  componentDidUpdate(prevProps, prevState) {
    this.stylingActive();
    this.coloredBut();
    if (prevState.colors.primary !== this.state.colors.primary
      || prevState.colors.secondary !== this.state.colors.secondary) {
      this.props.onPickColor(this.state.colors.primary, this.state.colors.secondary);
    }
    if (prevState.colors === this.state.colors && this.props.colors !== prevProps.colors) {
      this.setState({
        colors: {
          primary: this.props.colors.primary,
          secondary: this.props.colors.secondary,
        },
      });
    }
  }

  setColorPrim(ev) {
    const primary = ev.target.value;
    this.setState((state) => {
      const { secondary } = state.colors;
      return ({
        colors: {
          primary,
          secondary,
        },
      });
    });
  }

  setColorSec(ev) {
    const secondary = ev.target.value;
    this.setState((state) => {
      const { primary } = state.colors;
      return ({
        colors: {
          primary,
          secondary,
        },
      });
    });
  }

  changeColor(inputColor) {
    const input = this.refs[inputColor];
    act(() => input.dispatchEvent(new MouseEvent('click')));
  }

  reverseColors() {
    this.setState((state) => {
      const primary = state.colors.secondary;
      const secondary = state.colors.primary;
      return ({
        colors: {
          primary,
          secondary,
        },
      });
    });
  }

  stylingActive() {
    const { activeTool } = this.props || undefined;
    const tools = document.querySelectorAll('.tool');
    tools.forEach((tool) => {
      if (tool.classList.contains(activeTool)) {
        tool.parentElement.classList.add('active');
      } else {
        tool.parentElement.classList.remove('active');
      }
    });
  }

  coloredBut() {
    const colorButPrim = document.querySelector('.prim');
    const colorButSec = document.querySelector('.sec');
    colorButPrim.style.backgroundColor = this.state.colors.primary;
    colorButSec.style.backgroundColor = this.state.colors.secondary;
  }

  render() {
    const { tools } = this.state;
    return (
      <div className="tools">
        <h2>Tools</h2>
        <ul
          className="main"
        >
          {tools.map(tool =>
            <li key={tool.id}>
              <Tool
                name={tool.name}
                onActivate = {this.props.onActivateTool}
              />
            </li>)}
        </ul>
        <div className="colors">
          <button
            type="button"
            className="prim"
            onClick={() => this.changeColor('colorPrim')}
          />
          <button
            type="button"
            className="sec"
            onClick={() => this.changeColor('colorSec')}
          />
          <button
            type="button"
            className="reverse"
            onClick={() => this.reverseColors()}
          />
          <input
            ref="colorPrim"
            type="color"
            value={this.state.colors.primary}
            onChange={ev => this.setColorPrim(ev)}
          />
          <input
            ref="colorSec"
            type="color"
            value={this.state.colors.secondary}
            onChange={ev => this.setColorSec(ev)}
          />
        </div>
      </div>
    );
  }
}

export default Tools;
