import React from 'react';
import Backround from '../canvas/background';
import '../../styles/animation.scss';

class Animation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      rate: 6,
    };
  }

  componentDidMount() {
    if (this.props.arrFrames.length < 1) {
      const { video } = this.refs;
      video.style.visibility = "hidden";
    }
    const { screen } = this.refs;
    screen.style.height = `${screen.previousSibling.clientHeight}px`;
  }

  componentDidUpdate(prevProps, prevState) {
    const { video } = this.refs;
    if (this.props.arrFrames.length > 0) {
      video.style.visibility = "visible";
    } else {
      video.style.visibility = "hidden";
    }
    this.showVideo(this.props.arrFrames, this.state.rate);
    if (this.state.rate !== prevState.rate) {
      this.props.updateRate(this.state.rate);
    }
  }

  setValue(e) {
    // let {value} = e.target;
    // value = +value > 24 ? 24 :
    //   +value < 0 ? 0 : +value;
    return (
      this.setState({
        rate: +e.target.value,
      })
    );
  }

  showVideo(frames, rate) {
    const { screen } = this.refs;
    if (window.timerFps) {
      clearInterval(window.timerFps);
    }
    if (rate > 0 && frames.length > 0) {
      function show() {
        screen.style.backgroundImage = `url('${frames[i]}')`;
        screen.style.height = `${screen.previousSibling.clientHeight}px`;
        if (i >= frames.length - 1) {
            i = 0;
        } else {
            i++;
        }
      }
      window.timerFps = setInterval(() => show(), 1000 / rate);
      let i = 0;
    }
  }

  render() {
    return (
      <div className="animation">
        <h2>Animation</h2>
        <div
          className="video"
          ref="video"
        >
          <div
            className="canvas-wrap"
            onClick={() => {this.refs.canvasWrap.requestFullscreen()}}
            ref="canvasWrap"
          >
            <Backround
              canvasDim={this.props.canvasDim}
            />
            <div
              className="canvas"
              ref="screen"
            />
            <button
              type="button"
              className="close-fullscreen"
              onClick={() => document.exitFullscreen()}
            >
              X
            </button>
          </div>
        </div>
        <div className="fps">
          <p>fps</p>
          <input
            type="value"
            value={this.state.rate}
            min="0"
            max="24"
            onChange={e => this.setValue(e)}
          />
        </div>
      </div>
    );
  }
}

export default Animation;
