import React from 'react';
import '../../styles/tablet.scss';
import Canvas from './Canvas.jsx';

class Tablet extends React.Component {
  render() {
    const isFrame = this.props.frameId !== undefined;
    return (
      <div
        className="tablet"
      >
        {isFrame
          ? <Canvas
            canvasDim={this.props.canvasDim}
            frameId={this.props.frameId}
            tool={this.props.tool}
            colors={this.props.colors}
            onPickColor={this.props.onPickColor}
            onCanvasChange={this.props.onCanvasChange}
            onChangeCoords={this.props.onChangeCoords}
          />
          : <div>
            <p>Add your first frame!</p>
          </div>}
      </div>
    );
  }
}

export default Tablet;
