import React from 'react';
import Background from '../canvas/background';
import '../../styles/frames.css';
import '../../styles/canvas.css';

class Frames extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      frames: [],
      framesInData: [],
      activeFrameN: undefined,
      activeCanvasId: undefined,
      operation: undefined,
      id: 0,
      updateFramesInData: false,
    };
  }

  componentDidUpdate(prevProps, prevState) {
    const prevActiveCanvasId = prevState.activeCanvasId;
    const canvasOfActiveFrame = document.getElementById(this.state.activeCanvasId);
    if (this.state.updateFramesInData) {
      this.updateFramesInData();
    }
    if (this.state.operation === 'copy') {
      // !Update ctx frame from copy frame
      const canvasSource = this.refs.frames.querySelectorAll('.canvas')[this.state.activeFrameN - 1];
      const canvasTarget = document.getElementById(this.state.activeCanvasId);
      const ctx = canvasTarget.getContext('2d');
      ctx.clearRect(0, 0, this.props.canvasDim.width, this.props.canvasDim.height);
      ctx.drawImage(canvasSource, 0, 0);
      this.setState({
        operation: undefined,
      });
    }
    if (this.state.activeCanvasId !== prevActiveCanvasId) {
      // !Update canvas of tablet
      this.props.onFramesChange(this.state.activeCanvasId);
    }
    if (this.state.activeCanvasId === prevActiveCanvasId && this.props.updateFrame) {
      // !Update ctx frame from tablet
      const canvasSource = document.getElementById('tablet-canvas');
      const ctx = canvasOfActiveFrame.getContext('2d');
      ctx.clearRect(0, 0, this.props.canvasDim.width, this.props.canvasDim.height);
      ctx.drawImage(canvasSource, 0, 0);
      this.updateFramesInData();
    }
    if (this.state.framesInData !== prevState.framesInData) {
      // !Update animation
      this.props.onFramesDataChange(this.state.framesInData);
    }
    if (this.state.frames.length > 0) {
      const frames = document.querySelectorAll('.canvas');
      frames.forEach((el) => {
        if (el.id === this.state.activeCanvasId.toString()) {
          el.parentNode.classList.add('active');
        } else {
          el.parentNode.classList.remove('active');
        }
      });
    }
  }

  updateFramesInData() {
    const { activeCanvasId } = this.state;
    const { activeFrameN } = this.state;
    const activeFrameInData = document.getElementById(activeCanvasId).toDataURL('image/png');
    this.setState((state) => {
      const beforePos = state.framesInData.slice(0, activeFrameN);
      const afterPos = state.framesInData.slice(activeFrameN + 1);
      const framesInData = beforePos.concat(activeFrameInData, afterPos);
      return {
        framesInData,
        updateFramesInData: false,
      };
    });
  }

  addNewFrame() {
    const currentId = this.state.id;
    const canv = <canvas
      id={currentId}
      className="canvas"
      width={this.props.canvasDim.width}
      height={this.props.canvasDim.height}
      onClick={(ev) => {
        const id = ev.target.id;
        this.activateFrame(id);
      }}
    />;
    this.setState((state) => {
      const frames = state.frames.concat(canv);
      const id = state.id + 1;
      return {
        frames,
        activeCanvasId: currentId,
        activeFrameN: frames.length - 1,
        id,
        updateFramesInData: true,
      };
    });
  }

  activateFrame(id) {
    this.setState((state) => {
      const pos = state.frames.findIndex(el => el.props.id.toString() === id);
      return {
        activeCanvasId: id,
        activeFrameN: pos,
      };
    });
  }

  changeFramesSequence() {
  }

  deleteFrame(pos) {
    this.setState((state) => {
      const beforePos = state.frames.slice(0, pos);
      const afterPos = state.frames.slice(pos + 1);
      const frames = beforePos.concat(afterPos);
      const beforePosData = state.framesInData.slice(0, pos);
      const afterPosData = state.framesInData.slice(pos + 1);
      const framesInData = beforePosData.concat(afterPosData);
      const activePos = frames.length <= 0
        ? undefined
        : this.state.activeFrameN - 1 < 0
        ? 0
        : this.state.activeFrameN - 1;
      const id = activePos !== undefined ? frames[activePos].props.id : undefined;
      return {
        frames,
        framesInData,
        activeFrameN: activePos,
        activeCanvasId: id,
      };
    });
  }

  duplicateFrame(id, pos) {
    const currentId = this.state.id;
    const source = this.state.frames.filter(frame => frame.props.id === id);
    const clone = React.cloneElement(source[0], { id: currentId });
    this.setState((state) => {
      const beforePos = state.frames.slice(0, pos + 1);
      const afterPos = state.frames.slice(pos + 1);
      const frames = beforePos.concat(clone, afterPos);
      const cloneData = state.framesInData[pos];
      const beforePosData = state.framesInData.slice(0, pos + 1);
      const afterPosData = state.framesInData.slice(pos + 1);
      const framesInData = beforePosData.concat(cloneData, afterPosData);
      const idN = state.id + 1;
      return {
        frames,
        framesInData,
        activeCanvasId: currentId,
        activeFrameN: pos + 1,
        operation: 'copy',
        id: idN,
      };
    });
  }

  render() {
    return (
      <div
        ref="frames"
        className="frames"
      >
        <h2>Frames</h2>
        <ul>
          {this.state.frames.map((frame, pos) =>
            <li
              className="frame"
              key={frame.props.id}
            >
              <Background
                canvasDim={this.props.canvasDim}
              />
              {frame}
              <div className="frame-wrapper">
                 <div className='frame-num'>{pos + 1}</div>
                 <div
                   className='frame-del'
                   onClick={() => this.deleteFrame(pos)}
                 ></div>
                 <div className='frame-move'></div>
                 <div
                   className='frame-copy'
                   onClick={() => this.duplicateFrame(frame.props.id, pos)}
                 ></div>
               </div>
            </li>)}
        </ul>
        <button
          type="button"
          className="add-frame"
          onClick={() => this.addNewFrame()}
        >
          Add new frame
        </button>
      </div>
    );
  }
}

export default Frames;
