import React from 'react';
import '../../styles/canvas.css';
import Background from '../canvas/background';

class Canvas extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      cursor: {
        posX: false,
        posY: false,
      },
      canvasWillUpdated: false,
    };
  }

  componentDidMount() {
    this.canvasEl = document.getElementById('tablet-canvas');
    this.ctx = this.canvasEl.getContext('2d');
    this.canvasServ = document.getElementById('canvas-service');
    this.ctxS = this.canvasServ.getContext('2d');
  }

  componentDidUpdate(prevProps) {
    if (!prevProps || this.props.frameId !== prevProps.frameId) {
      const canvasSource = document.getElementById(this.props.frameId);
      this.ctx.clearRect(0, 0, this.props.canvasDim.width, this.props.canvasDim.height);
      this.ctx.drawImage(canvasSource, 0, 0);
    }
    if (this.state.canvasWillUpdated) {
      this.props.onCanvasChange(this.state.canvasWillUpdated);
      this.setState({
        canvasWillUpdated: false,
      });
    }
  }

  setOperation(ev) {
    const { tool } = this.props || undefined;
    if (ev.button === 0) {
      this.ctx.fillStyle = this.props.colors.primary;
      this.ctx.strokeStyle = this.props.colors.primary;
      this.ctxS.fillStyle = this.props.colors.primary;
      this.ctxS.strokeStyle = this.props.colors.primary;
    } else if (ev.button === 2) {
      this.ctx.fillStyle = this.props.colors.secondary;
      this.ctx.strokeStyle = this.props.colors.secondary;
      this.ctxS.fillStyle = this.props.colors.secondary;
      this.ctxS.strokeStyle = this.props.colors.secondary;
    } else {
      return false;
    }
    switch (tool) {
      case 'pen':
        this.draw();
        break;
      case 'bucket':
        this.bucket();
        break;
      case 'eraser':
        this.clear();
        break;
      case 'rectangle':
        this.rectangle(true);
        break;
      case 'border':
        this.rectangle();
        break;
      case 'line':
        this.line(true);
        break;
      case 'circle':
        this.drawCircle(true);
        break;
      case 'disc':
        this.drawCircle();
        break;
      case 'move':
        this.moveCanv();
        break;
      case 'lighten':
        this.lightenPixel(ev);
        break;
      case 'picker':
        this.setColor(ev);
        break;
      default:
        break;
    }
    this.canvasEl.oncontextmenu = function (e) {
      e.preventDefault();
    };
  }

  getCursorPosition(ev) {
    const box = ev.target.getBoundingClientRect();
    const boxCoord = {
      top: box.top + pageYOffset,
      bottom: box.bottom + pageYOffset,
      left: box.left + pageXOffset,
      right: box.right + pageXOffset,
    };
    const cellWidth = ((boxCoord.right - boxCoord.left) / this.props.canvasDim.width);
    const cellHeight = ((boxCoord.bottom - boxCoord.top) / this.props.canvasDim.height);
    const cellPosX = Math.ceil((ev.pageX - boxCoord.left) / cellWidth);
    const cellPosY = Math.ceil((ev.pageY - boxCoord.top) / cellHeight);
    if (cellPosX !== this.state.cursor.posX || cellPosY !== this.state.cursor.posY) {
      this.setState({
        cursor: {
          posX: cellPosX,
          posY: cellPosY,
        },
      });
    }
  }

  getPixelColor() {
    const startX = this.state.cursor.posX - 1;
    const startY = this.state.cursor.posY - 1;
    return this.ctx.getImageData(startX, startY, 1, 1).data;
  }

  convertRGBAtoHEXa(r, g, b, a) {
    let rHEXa = r.toString(16);
    let gHEXa = g.toString(16);
    let bHEXa = b.toString(16);
    let aHEXa = Math.round(a / 255).toString(16);
    if (rHEXa.length === 1) rHEXa = "0" + rHEXa;
    if (gHEXa.length === 1) gHEXa = "0" + gHEXa;
    if (bHEXa.length === 1) bHEXa = "0" + bHEXa;
    if (aHEXa.length === 1) aHEXa = "0" + aHEXa;
    return '#' + rHEXa + gHEXa + bHEXa; // + aHEXa;
  }

  convertRGBAtoHSLA(r, g, b, a) {
    r /= 255;
    g /= 255;
    b /= 255;
    let cmin = Math.min(r,g,b);
    let cmax = Math.max(r,g,b);
    let delta = cmax - cmin;
    let h = 0;
    let s = 0;
    let l = 0;
    if (delta == 0) {
      h = 0;
    } else if (cmax == r) {
      h = ((g - b) / delta) % 6;
    } else if (cmax == g) {
      h = (b - r) / delta + 2;
    } else {
      h = (r - g) / delta + 4;
    }
    h = Math.round(h * 60);
    if (h < 0) {
      h += 360;
    }
    l = (cmax + cmin) / 2;
    s = delta == 0 ? 0 : delta / (1 - Math.abs(2 * l - 1));
    s = +(s * 100).toFixed(1);
    l = +(l * 100).toFixed(1);
    // return "hsl(" + h + "," + s + "%," + l + "%)";
    return [h, s, l];
  }

  setColor(ev) {
    const color = this.getPixelColor();
    const colorInRGBA = `rgba(${color[0]},${color[1]},${color[2]},${color[3]})`;
    const colorInHex = this.convertRGBAtoHEXa(+`${color[0]}`, +`${color[1]}`, +`${color[2]}`, +`${color[3]}`);
    if (ev.button === 0) {
      this.props.onPickColor(colorInHex, this.props.colors.secondary);
    } else if (ev.button === 2) {
      this.props.onPickColor(this.props.colors.primary, colorInHex);
    } else {
      return false;
    }
    this.canvasEl.onmouseup = () => { return false; };
  }

  draw() {
    let x = this.state.cursor.posX - 1;
    let y = this.state.cursor.posY - 1;
    this.drawCell(this.ctx, x, y);
    let newX;
    let newY;
    this.canvasEl.onmousemove = () => {
      newX = this.state.cursor.posX - 1;
      newY = this.state.cursor.posY - 1;
      if (x !== newX || y !== newY) {
        this.drawBresenhamsLine(this.ctx, x, y, newX, newY);
      }
      x = newX;
      y = newY;
    };
    this.canvasEl.onmouseup = () => {
      this.setState({
        canvasWillUpdated: true,
      });
    };
  }

  bucket() {
    const { ctx } = this;
    const width = this.props.canvasDim.width;
    const height = this.props.canvasDim.height;
    const startX = this.state.cursor.posX - 1;
    const startY = this.state.cursor.posY - 1;
    let pixelStack = [[startX, startY]];
    const imgDataRGBA = this.ctx.getImageData(startX, startY, 1, 1).data;

    function compareWithTarget(data) {
      if (imgDataRGBA[0] === data[0]
        && imgDataRGBA[1] === data[1]
        && imgDataRGBA[2] === data[2]
        && imgDataRGBA[3] === data[3]
      ) {
        return true;
      }
      return false;
    }

    function findCellAround(x, y) {
      if (y > 0 && compareWithTarget(ctx.getImageData(x, y - 1, 1, 1).data)) {
        pixelStack.push([x, y - 1]);
      }
      if (x < width - 1 && compareWithTarget(ctx.getImageData(x + 1, y, 1, 1).data)) {
        pixelStack.push([x + 1, y]);
      }
      if (y < height - 1 && compareWithTarget(ctx.getImageData(x, y + 1, 1, 1).data)) {
        pixelStack.push([x, y + 1]);
      }
      if (x > 0 && compareWithTarget(ctx.getImageData(x - 1, y, 1, 1).data)) {
        pixelStack.push([x - 1, y]);
      }
    }

    this.canvasEl.onmouseup = () => {
      while (pixelStack.length > 0) {
        const activePixel = pixelStack.pop();
        findCellAround(activePixel[0], activePixel[1]);
        ctx.fillRect(activePixel[0], activePixel[1], 1, 1);
      }
      this.setState({
        canvasWillUpdated: true,
      });
    };
  }

  clear() {
    let x = this.state.cursor.posX - 1;
    let y = this.state.cursor.posY - 1;
    this.clearCell(this.ctx, x, y);
    let newX;
    let newY;
    this.canvasEl.onmousemove = () => {
      newX = this.state.cursor.posX - 1;
      newY = this.state.cursor.posY - 1;
      if (x !== newX || y !== newY) {
        this.drawBresenhamsLine(this.ctx, x, y, newX, newY, true);
      }
      x = newX;
      y = newY;
    };
    this.canvasEl.onmouseup = () => {
      this.setState({
        canvasWillUpdated: true,
      });
    };
  }

  rectangle(border) {
    this.canvasServ.style.visibility = 'visible';
    const x = this.state.cursor.posX - 0.5;
    const y = this.state.cursor.posY - 0.5;
    this.drawCell(this.ctxS, x - 0.5, y - 0.5);
    this.drawCell(this.ctx, x - 0.5, y - 0.5);
    let newX;
    let newY;
    let width;
    let height;
    let modX;
    let modY;
    this.canvasServ.onmousemove = () => {
      newX = this.state.cursor.posX - 0.5;
      newY = this.state.cursor.posY - 0.5;
      if (x !== newX || y !== newY) {
        width = Math.abs(newX - x);
        height = Math.abs(newY - y);
        modX = newX - x > 0 ? 1 : newX - x === 0 ? 0 : -1;
        modY = newY - y > 0 ? 1 : newY - y === 0 ? 0 : -1;
        this.ctxS.clearRect(0, 0, this.props.canvasDim.width, this.props.canvasDim.height);
        if (border || modX === 0 || modY === 0) {
          if (modX >= 0 && modY >= 0) { this.ctxS.fillRect(x - 0.5, y - 0.5, width + 1, height + 1); }
          if (modX >= 0 && modY < 0) { this.ctxS.fillRect(x - 0.5, newY - 0.5, width + 1, height + 1); }
          if (modX < 0 && modY >= 0) { this.ctxS.fillRect(newX - 0.5, y - 0.5, width + 1, height + 1); }
          if (modX < 0 && modY < 0) { this.ctxS.fillRect(newX - 0.5, newY - 0.5, width + 1, height + 1); }
        } else {
          if (modX > 0 && modY > 0) { this.ctxS.strokeRect(x, y, width, height); }
          if (modX > 0 && modY < 0) { this.ctxS.strokeRect(x, newY, width, height); }
          if (modX < 0 && modY > 0) { this.ctxS.strokeRect(newX, y, width, height); }
          if (modX < 0 && modY < 0) { this.ctxS.strokeRect(newX, newY, width, height); }
        }
      } else {
        this.ctxS.clearRect(0, 0, this.props.canvasDim.width, this.props.canvasDim.height);
        this.drawCell(this.ctxS, x - 0.5, y - 0.5);
      }
    };
    this.canvasServ.onmouseup = () => {
      if (x !== newX || y !== newY) {
        if (border || modX === 0 || modY === 0) {
          if (modX >= 0 && modY >= 0) { this.ctx.fillRect(x - 0.5, y - 0.5, width + 1, height + 1); }
          if (modX >= 0 && modY < 0) { this.ctx.fillRect(x - 0.5, newY - 0.5, width + 1, height + 1); }
          if (modX < 0 && modY >= 0) { this.ctx.fillRect(newX - 0.5, y - 0.5, width + 1, height + 1); }
          if (modX < 0 && modY < 0) { this.ctx.fillRect(newX - 0.5, newY - 0.5, width + 1, height + 1); }
        } else {
          if (modX > 0 && modY > 0) { this.ctx.strokeRect(x, y, width, height); }
          if (modX > 0 && modY < 0) { this.ctx.strokeRect(x, newY, width, height); }
          if (modX < 0 && modY > 0) { this.ctx.strokeRect(newX, y, width, height); }
          if (modX < 0 && modY < 0) { this.ctx.strokeRect(newX, newY, width, height); }
        }
      }
      this.canvasServ.style.visibility = 'hidden';
      this.ctxS.clearRect(0, 0, this.props.canvasDim.width, this.props.canvasDim.height);
      this.setState({
        canvasWillUpdated: true,
      });
    };
  }

  line() {
    this.canvasServ.style.visibility = 'visible';
    const x = this.state.cursor.posX - 1;
    const y = this.state.cursor.posY - 1;
    this.drawCell(this.ctx, x, y);
    let newX;
    let newY;
    this.canvasServ.onmousemove = () => {
      newX = this.state.cursor.posX - 1;
      newY = this.state.cursor.posY - 1;
      if (x !== newX || y !== newY) {
        this.ctxS.clearRect(0, 0, this.props.canvasDim.width, this.props.canvasDim.height);
        this.drawBresenhamsLine(this.ctxS, x, y, newX, newY);
      }
    };
    this.canvasServ.onmouseup = () => {
      this.drawBresenhamsLine(this.ctx, x, y, newX, newY);
      this.canvasServ.style.visibility = 'hidden';
      this.ctxS.clearRect(0, 0, this.props.canvasDim.width, this.props.canvasDim.height);
      this.setState({
        canvasWillUpdated: true,
      });
    };
  }

  drawCircle(disc) {
    this.canvasServ.style.visibility = 'visible';
    let x = this.state.cursor.posX - 0.5;
    let y = this.state.cursor.posY - 0.5;
    let newX;
    let newY;
    this.canvasServ.onmousemove = () => {
      newX = this.state.cursor.posX - 0.5;
      newY = this.state.cursor.posY - 0.5;
      if (x !== newX || y !== newY) {
        this.ctxS.clearRect(0, 0, this.props.canvasDim.width, this.props.canvasDim.height);
        // this.ctxS.lineWidth = 3;
        const radius = Math.sqrt((newX - x) * (newX - x) + (newY - y) * (newY - y))
        this.ctxS.beginPath();
        this.ctxS.arc(x, y, radius, 0, 2 * Math.PI);
        if (disc) this.ctxS.fill();
        this.ctxS.stroke();
      } else {
        this.ctxS.clearRect(0, 0, this.props.canvasDim.width, this.props.canvasDim.height);
        this.ctxS.fillRect(x - 0.5, y - 0.5, 1, 1);
      }
    };
    this.canvasServ.onmouseup = () => {
      if (x !== newX || y !== newY) {
        const radius = Math.sqrt((newX - x) * (newX - x) + (newY - y) * (newY - y))
        this.ctx.beginPath();
        this.ctx.arc(x, y, radius, 0, 2 * Math.PI);
        if (disc) this.ctx.fill();
        this.ctx.stroke();
      } else {
        this.ctx.fillRect(x - 0.5, y - 0.5, 1, 1);
      }
      this.canvasServ.style.visibility = 'hidden';
      this.ctxS.clearRect(0, 0, this.props.canvasDim.width, this.props.canvasDim.height);
      this.setState({
        canvasWillUpdated: true,
      });
    };
  }

  moveCanv() {
    let flag = true;
    const x = this.state.cursor.posX - 1;
    const y = this.state.cursor.posY - 1;
    const { width } = this.props.canvasDim;
    const { height } = this.props.canvasDim;
    const dataCtx = this.ctx.getImageData(0, 0, width, height);
    this.canvasEl.onmousemove = () => {
      const newX = this.state.cursor.posX - 1;
      const newY = this.state.cursor.posY - 1;
      if (x !== newX || y !== newY) {
        if (flag) {
          const dx = x - newX;
          const dy = y - newY;
          this.ctx.clearRect(0, 0, width, height);
          this.ctx.putImageData(dataCtx, -dx, -dy);
        }
      }
    };
    this.canvasEl.onmouseup = () => {
      flag = false;
      this.setState({
        canvasWillUpdated: true,
      });
    };
  }

  lightenPixel(ev) {
    let flag = true;
    let color = this.getPixelColor();
    let colorHSLA = this.convertRGBAtoHSLA(+`${color[0]}`, +`${color[1]}`, +`${color[2]}`, +`${color[3]}`);
    let x = this.state.cursor.posX - 1;
    let y = this.state.cursor.posY - 1;
    let h = colorHSLA[0];
    let s = colorHSLA[1];
    let l = colorHSLA[2];
    const sign = ev.button === 0 ? 1 : -1;
    let HSLAl = `hsl(${h},${s}%,${l + 2 * sign}%)`;
    this.ctx.fillStyle = HSLAl;
    if (color[3] !== 0) {
      this.ctx.fillRect(x, y, 1, 1);
    }
    this.canvasEl.onmousemove = () => {
      const newX = this.state.cursor.posX - 1;
      const newY = this.state.cursor.posY - 1;
      if (x !== newX || y !== newY) {
        x = newX;
        y = newY;
        if (flag) {
          color = this.getPixelColor();
          colorHSLA = this.convertRGBAtoHSLA(+`${color[0]}`, +`${color[1]}`, +`${color[2]}`, +`${color[3]}`);
          h = colorHSLA[0];
          s = colorHSLA[1];
          l = colorHSLA[2];
          HSLAl = `hsl(${h},${s}%,${l + 2 * sign}%)`;
          this.ctx.fillStyle = HSLAl;
          if (color[3] !== 0) {
            this.ctx.fillRect(newX, newY, 1, 1);
          }
        }
      }
    };
    this.canvasEl.onmouseup = () => {
      flag = false;
      this.setState({
        canvasWillUpdated: true,
      });
    };
  }

  drawBresenhamsLine(ctx, x1, y1, x2, y2, clear) {
    let x; let y; let xe; let ye; let i;
    const dx = x2 - x1;
    const dy = y2 - y1;
    const dx1 = Math.abs(dx);
    const dy1 = Math.abs(dy);
    let px = 2 * dy1 - dx1;
    let py = 2 * dx1 - dy1;
    if (dy1 <= dx1) {
      if (dx >= 0) {
        x = x1; y = y1; xe = x2;
      } else {
        x = x2; y = y2; xe = x1;
      }
      clear ? this.clearCell(ctx, x, y) : this.drawCell(ctx, x, y);
      for (i = 0; x < xe; i++) {
        x += 1;
        if (px < 0) {
          px += 2 * dy1;
        } else {
          if ((dx < 0 && dy < 0) || (dx > 0 && dy > 0)) {
            y += 1;
          } else {
            y -= 1;
          }
          px += 2 * (dy1 - dx1);
        }
        clear ? this.clearCell(ctx, x, y) : this.drawCell(ctx, x, y);
      }
    } else {
      if (dy >= 0) {
        x = x1; y = y1; ye = y2;
      } else {
        x = x2; y = y2; ye = y1;
      }
      clear ? this.clearCell(ctx, x, y) : this.drawCell(ctx, x, y);
      for (i = 0; y < ye; i++) {
        y += 1;
        if (py <= 0) {
          py += 2 * dx1;
        } else {
          if ((dx < 0 && dy < 0) || (dx > 0 && dy > 0)) {
            x += 1;
          } else {
            x -= 1;
          }
          py += 2 * (dx1 - dy1);
        }
        clear ? this.clearCell(ctx, x, y) : this.drawCell(ctx, x, y);
      }
    }
  }

  drawCell(ctx, x, y) {
    ctx.fillRect(x, y, 1, 1);
  }

  clearCell(ctx, x, y) {
    ctx.clearRect(x, y, 1, 1);
  }

  render() {
    return (
      <div
        className="canvas-wrap"
      >
        <Background
          canvasDim={this.props.canvasDim}
        />
        <canvas
          className="canvas-service"
          id="canvas-service"
          width={this.props.canvasDim.width}
          height={this.props.canvasDim.height}
          onMouseMove={ev => this.getCursorPosition(ev)}
        />
        <canvas
          className="canvas"
          id="tablet-canvas"
          width={this.props.canvasDim.width}
          height={this.props.canvasDim.height}
          onMouseDown={ev => this.setOperation(ev)}
          onMouseMove={ev => this.getCursorPosition(ev)}
        />
      </div>
    );
  }
}

export default Canvas;
