import React from 'react';
import '../../styles/popup.css';

class Popup extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDim: '32',
    };
    this.handleDimChange = this.handleDimChange.bind(this);
  }

  handleDimChange(ev) {
    this.setState({
      selectedDim: ev.target.value,
    });
  }

  render() {
    const { setDim } = this.props;
    const { resize } = this.props;
    const { onCanvasDimChange } = this.props;
    return (
      <div
        className={`popup-wrap  ${(setDim ? 'show' : 'hidden')}`}
      >
        <div
          className="popup"
        >
          <form>
            <p>Canvas dimensions</p>
            <input
              name="dim"
              type="radio"
              value="32"
              checked={this.state.selectedDim === '32'}
              onChange={this.handleDimChange}
            />
              32x32
            <br />
            <input
              name="dim"
              type="radio"
              value="64"
              checked={this.state.selectedDim === '64'}
              onChange={this.handleDimChange}
            />
              64x64
            <br />
            <input
              name="dim"
              type="radio"
              value="128"
              checked={this.state.selectedDim === '128'}
              onChange={this.handleDimChange}
            />
              128x128
            <br />
            <div>
              {resize
                ? 'resize'
                : ''}
            </div>
          </form>
          <button
            type="button"
            onClick={() => onCanvasDimChange(+this.state.selectedDim)}
          >
            ok
          </button>
        </div>
      </div>
    );
  }
}

export default Popup;
