import React from 'react';
import '../../styles/footer.css';

class Footer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      cursorCoord: {
        X: undefined,
        Y: undefined,
      },
    };
    this.getCursorPosition = this.getCursorPosition.bind(this);
    this.resetCursorPosition = this.resetCursorPosition.bind(this);
  }

  componentDidUpdate() {
    if (document.querySelector('.tablet').querySelector('.canvas')) {
      const canvas = document.querySelector('.tablet').querySelector('.canvas');
      canvas.onmousemove = this.getCursorPosition;
      canvas.onmouseout = this.resetCursorPosition;
    }
  }

  getCursorPosition(ev) {
    const canvas = ev.target;
    const box = canvas.getBoundingClientRect();
    const boxCoord = {
      top: box.top + pageYOffset,
      bottom: box.bottom + pageYOffset,
      left: box.left + pageXOffset,
      right: box.right + pageXOffset,
    };
    const cellWidth = (boxCoord.right - boxCoord.left) / canvas.width;
    const cellHeight = (boxCoord.bottom - boxCoord.top) / canvas.height;
    const cellPosX = Math.ceil((ev.pageX - boxCoord.left) / cellWidth);
    const cellPosY = Math.ceil((ev.pageY - boxCoord.top) / cellHeight);
    this.setState({
      cursorCoord: {
        X: cellPosX,
        Y: cellPosY,
      },
    });
  }

  resetCursorPosition() {
    this.setState({
      cursorCoord: {
        X: false,
        Y: false,
      },
    });
  }

  render() {
    const { canvasDim } = this.props;
    return (
      <div className="footer">
        <div
          className="canvas-dimension"
        >
          <button
            type="button"
            className="btn refresh"
            onClick={() => this.props.onUpdateDim()}
          />
          <p>
            {`${canvasDim.width || 'W'} x ${canvasDim.height || 'H'}`}
          </p>
        </div>
        <div
          className="cursor-position"
        >
          <p>
            {`${this.state.cursorCoord.X || 'X'} : ${this.state.cursorCoord.Y || 'Y'}`}
          </p>
        </div>
      </div>
    );
  }
}

export default Footer;
